<template>
  <div v-if="quest" style="padding: 4px;">
    <v-dialog v-model="dialog" max-width="500" persistent scrollable>
      <v-card style="height:400px;" v-if="!done">
        <v-card-title>{{quest.name}}</v-card-title>
        <v-card-subtitle>Remove the following items from stock?</v-card-subtitle>
        <v-divider></v-divider>
        <v-card-text>
          <!-- <h3>The following tasks will be marked as done:</h3>
              <div>
                <ul>
                  <li>"{{dialogContent.item.name}}"</li>
                  <li v-for="c in dialogContent.item.children" :key="c.id">"{{c.name}}"</li>
                </ul>
              </div>
          <br />-->
          <v-row v-for="(value, item) in requiredItems" :key="item.id">
            <ItemBox
              v-for="i in value.raid&&value.raid.amount?value.raid.amount:0"
              :item="item"
              :value="value"
              :raid="true"
              :checked="$store.state.stocked[value.item.id]?i<=$store.state.stocked[value.item.id].raid:false"
              :key="item.id+'r'+i"
              :quests="value.raid.quests"
              :hideout="value.raid.hideout"
              :cramped="false"
            />
            <ItemBox
              v-for="i in value.nonraid&&value.nonraid.amount?value.nonraid.amount:0"
              :item="item"
              :value="value"
              :raid="false"
              :checked="$store.state.stocked[value.item.id]?i<=$store.state.stocked[value.item.id].nonraid:false"
              :key="item.id+'n'+i"
              :quests="value.nonraid.quests"
              :hideout="value.nonraid.hideout"
              :cramped="false"
            />
          </v-row>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="toggleQuest(true)">Yes</v-btn>
          <v-btn color="blue darken-1" text @click="toggleQuest(false)">No</v-btn>
          <v-btn color="red darken-1" text @click="dialog=false">Cancel</v-btn>
        </v-card-actions>
      </v-card>
      <v-card style="height: 300px;" v-else>
        <v-card-title class="headline">{{quest.name}}</v-card-title>
        <v-card-text>
          <v-card-subtitle>Undo progress?</v-card-subtitle>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="toggleQuest(false)">Ok</v-btn>
          <v-btn color="red darken-1" text @click="dialog=false">Cancel</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-card class="mx-auto">
      <v-card-actions>
        <v-list-item-content :class="{done}">
          <v-list-item-title class="headline">{{quest.name}}</v-list-item-title>
          <v-list-item-subtitle>{{quest.from }}</v-list-item-subtitle>
        </v-list-item-content>
        <v-spacer></v-spacer>
        <v-btn icon :href="quest.url" target="_blank" v-if="quest.url">
          <v-icon>mdi-link</v-icon>
        </v-btn>
        <v-btn color="primary" @click="dialog=true" v-if="!done">Complete</v-btn>
        <v-btn color="primary" @click="dialog=true" v-else>Undo</v-btn>
      </v-card-actions>
      <v-card-text v-if="(showItems || showObjectives)">
        <v-row>
          <v-col :cols="showItems? 6 : 12" v-if="showObjectives">
            <ul>
              <li v-for="objective in quest.objectives" :key="objective.id">{{objective.name}}</li>
            </ul>
          </v-col>
          <v-col :cols="showObjectives? 6 : 12" v-if="showItems">
            <div v-for="(value, e) in requiredItems" :key="e.id">
              <ItemBox
                v-for="i in value.raid&&value.raid.amount?value.raid.amount:0"
                :item="e"
                :value="value"
                :raid="true"
                :checked="$store.state.stocked[value.item.id]?i<=$store.state.stocked[value.item.id].raid:false"
                :key="e+'r'+i"
                :quests="value.raid.quests"
                :hideout="value.raid.hideout"
              />
              <ItemBox
                v-for="i in value.nonraid&&value.nonraid.amount?value.nonraid.amount:0"
                :item="e"
                :value="value"
                :raid="false"
                :checked="$store.state.stocked[value.item.id]?i<=$store.state.stocked[value.item.id].nonraid:false"
                :key="e+'n'+i"
                :quests="value.nonraid.quests"
                :hideout="value.nonraid.hideout"
              />
            </div>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
  </div>
</template>
<script>
import ItemBox from "./ItemBox.vue";
export default {
  components: { ItemBox },
  data: () => ({
    dialog: false
  }),
  props: {
    quest: Object,
    showItems: Boolean,
    showObjectives: Boolean,
    done: Boolean
  },
  computed: {
    requiredItems: function() {
      return this.staticData.getRequirementsForQuests([this.quest.id]).items;
    }
  },
  methods: {
    toggleQuest(removeFromStock) {
      if (removeFromStock) {
        this.$store.commit("removeItemsFromStock", this.requiredItems);
      }
      var doneQuests = this.$store.state.doneQuests;
      if (!this.done) {
        doneQuests.push(this.quest.id);
        this.$store.commit("setDoneQuests", doneQuests);
      } else {
        var index = doneQuests.indexOf(this.quest.id);
        if (index !== -1) {
          doneQuests.splice(index, 1);
        }
        this.$store.commit("setDoneQuests", doneQuests);
      }

      this.$emit("finishedQuest", this.quest, removeFromStock);
      this.dialog = false;
    }
  }
};
</script>
<style>
.done {
  color: green;
}
</style>