<template>
  <v-container>
    <v-row dense>
      <v-col>
        <v-expansion-panels accordion hover tile v-model="showOptions">
          <v-expansion-panel>
            <v-expansion-panel-header>Options</v-expansion-panel-header>
            <v-expansion-panel-content>
              <div class="header">Show quests:</div>
              <v-radio-group>
                <v-radio-group v-model="showQuests">
                  <v-radio label="active" value="active"></v-radio>
                  <v-radio label="done" value="done"></v-radio>
                  <v-radio label="locked" value="locked" disabled></v-radio>
                </v-radio-group>
              </v-radio-group>
              <div class="header">Show additional info:</div>
              <v-checkbox dense v-model="showItems" label="Quest items"></v-checkbox>
              <v-checkbox dense v-model="showObjectives" label="Quest objectives"></v-checkbox>

              <v-text-field :clearable="true" label="Filter" v-model="search"></v-text-field>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <quest
          v-for="quest in getQuestsFiltered"
          :key="quest.id"
          :quest="quest"
          :done="doneQuests.indexOf(quest.id)!==-1"
          :showItems="showItems"
          :showObjectives="showObjectives"
        />
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import Quest from "./Quest.vue";

export default {
  name: "Quests",
  components: { Quest },
  data: () => ({
    showOptions: 0,
    quests: [],
    doneQuests: [],
    dialog: false,
    dialogContent: null,
    showItems: false,
    showObjectives: false,
    showQuests: "active",
    ignore: false,
    search: ""
  }),
  computed: {
    storeDoneQuests: function() {
      return this.$store.state.doneQuests;
    },
    getQuestsFiltered: function() {
      var quests = [];
      if (this.showQuests === "active") {
        quests = this.staticData.getQuestsFiltered(
          this.doneQuests,
          0,
          0,
          this.$store.state.levels
        );
      }
      if (this.showQuests === "done") {
        quests = this.doneQuests.map(id => this.staticData.getQuest(id));
      }
      // if(this.showQuests === "locked"){
      // }
      if (this.search) {
        console.log(this.search);
        quests = quests.filter(
          q =>
            q.name.toLowerCase().includes(this.search.toLowerCase()) ||
            q.from.toLowerCase().includes(this.search.toLowerCase())
        );
      }
      return quests;
    }
  },
  watch: {
    storeDoneQuests: function(newVal) {
      this.ignore = true;
      this.doneQuests = newVal;
      this.ignore = false;
    },
    // doneQuests: function(oldVal, newVal) {
    // var added = newVal.filter(e => oldVal.indexOf(e) === -1);
    // var removed = oldVal.filter(e => newVal.indexOf(e) === -1);
    // if (!this.dialog && !this.ignore) {
    //   if (added && added.length > 0)
    //     this.toggle(this.staticData.getQuest(added[0]), true);
    //   else if (removed && removed.length > 0)
    //     this.toggle(this.staticData.getQuest(removed[0]), false);
    // }
    // },
    showOptions: function(newVal) {
      this.$store.commit("setShowOptionsQuests", newVal);
    }
  },
  created: function() {
    this.ignore = true;
    this.quests = this.staticData.getStarterQuests();

    this.doneQuests = this.$store.state.doneQuests;
    this.stocked = this.$store.state.stocked;
    this.showOptions = this.$store.state.showOptionsQuests;
  },
  mounted: function() {
    var vm = this;
    this.$nextTick().then(function() {
      vm.ignore = false;
    });
  },
  methods: {}
};
</script>
<style>
.indent {
  padding-left: 10px !important;
}
.header {
  font-weight: bold;
}
.v-application--is-ltr .v-treeview-node__checkbox {
  margin-top: 5px;
}

.v-treeview-node__root {
  align-items: flex-start;
}
.v-treeview-node__label:before {
  content: "\a0";
  display: block;
  padding: 2px 0;
  line-height: 1px;
}
.v-treeview-node__label p {
  padding: 0 10px;
}
.v-input--selection-controls {
  margin-top: 0px;
  padding-top: 0px;
}
</style>